import XTextarea from 'bh-mint-ui2/packages/textarea/index.js';
export default {
  name: 'page-radio',

  data() {
    return {
      value0: '',
      value1: '',
      value2: '值A',
      value3: '',
      textareaContent:''
    };
  },

  created() {
    this.options0 = ['选项A', '选项B', '选项C'];
    this.options1 = ['选项A', '选项B', '选项C'];
    this.options3 = ['选项A', '选项B', '选项C'];
    this.options2 = [
      {
        label: '被禁用',
        value: '值F',
        disabled: true
      },
      {
        label: '选项A',
        value: '值A'
      },
      {
        label: '选项B',
        value: '值B'
      }
    ];
  },
  components: {
    XTextarea
  }
};



